import React, { useRef, useEffect } from "react";

const MatrixBackground = () => {
    const canvasRef = useRef(null);

    useEffect(() => {
        const canvas = canvasRef.current;
        const context = canvas.getContext("2d");
        canvas.width = window.innerWidth;
        canvas.height = 400; // Set height to 400px

        const katakana =
            "";
        const latin = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
        const nums = "0123456789";
        const alphabet = katakana + latin + nums;

        const fontSize = 16;
        const columns = canvas.width / fontSize;
        const rainDrops = Array.from({ length: columns }, () => 1);

        const render = () => {
            // Create a semi-transparent background effect to simulate the trailing effect
            context.fillStyle = "rgba(245, 245, 245, 0.1)";
            context.fillRect(0, 0, canvas.width, canvas.height);

            context.font = fontSize + "px monospace";

            rainDrops.forEach((y, index) => {
                const text = alphabet.charAt(Math.floor(Math.random() * alphabet.length));
                const x = index * fontSize;

                // Calculate the opacity based on the vertical position (the lower the position, the lighter the letter)
                const opacity = 1 - y * fontSize / canvas.height;
                context.fillStyle = `rgba(${140 + (105 * (1 - opacity))}, ${140 + (105 * (1 - opacity))}, ${140 + (105 * (1 - opacity))}, ${Math.max(opacity, 0.2)})`;

                // Darker gray (#434343) to lighter

                // Draw the letter
                context.fillText(text, x, y * fontSize);

                // Reset the drop to the top randomly for the rain effect
                if (y * fontSize > canvas.height && Math.random() > 0.975) {
                    rainDrops[index] = 0;
                }
                rainDrops[index]++;
            });
        };

        const intervalId = setInterval(render, 50);

        return () => clearInterval(intervalId);
    }, []);

    return (
        <canvas
            ref={canvasRef}
            style={{
                position: "absolute", // Keep it absolute so it doesn't scroll
                top: 0,
                left: 0,
                zIndex: "0", // Behind main content
                width: "100vw",
                height: "500px", // Explicit height
                pointerEvents: "none", // No interaction
            }}
        />
    );
};

export default MatrixBackground;
