import React from "react";
import { Tag, Typography, Space } from "antd";
import { CodeOutlined, LaptopOutlined } from "@ant-design/icons";

const { Title } = Typography;

// Skills and Frameworks Data
const skills = ["JavaScript", "Java", "Python", "HTML", "CSS", "SQL", "C/C++"];
const frameworks = ["React", "Spring/Spring Boot", "Node.js", "Express", "React Native"];

const colors = ["magenta", "volcano", "orange", "gold", "green", "cyan", "blue", "purple", "red", "lime"];

const getUniqueColors = (items) => {
    const selectedColors = [];
    return items.map(() => {
        let color;
        do {
            color = colors[Math.floor(Math.random() * colors.length)];
        } while (selectedColors.includes(color));
        selectedColors.push(color);
        return color;
    });
};

const skillsColors = getUniqueColors(skills);
const frameworksColors = getUniqueColors(frameworks);

const Skills = () => {
    return (
        <div>
            {/* Skills Section */}
            <div style={{ marginBottom: "50px", marginTop: 20 }}>
                <Title level={3}>Skills</Title>
                <div
                    style={{
                        display: "flex",
                        flexWrap: "wrap",
                        maxWidth: "600px" // Adjust maxWidth as needed
                    }}
                >
                    {skills.map((skill, index) => (
                        <Tag key={index} color={skillsColors[index]} icon={<CodeOutlined />} style={{ margin: "4px" }}>
                            {skill}
                        </Tag>
                    ))}
                </div>
            </div>

            {/* Frameworks Section */}
            <section style={{ marginBottom: "50px" }}>
                <Title level={3}>Frameworks / Libraries</Title>
                <Space wrap>
                    {frameworks.map((framework, index) => (
                        <Tag key={index} color={frameworksColors[index]} icon={<LaptopOutlined />}>
                            {framework}
                        </Tag>
                    ))}
                </Space>
            </section>
        </div>
    );
};

export default Skills;
