import React, { useState, useEffect } from "react";
import { Layout, Card, Avatar, Button, Typography, Menu } from "antd";
import {
    GithubOutlined,
    LinkedinOutlined,
    InstagramOutlined,
    MailOutlined,
    PhoneOutlined,
    WhatsAppOutlined,
} from "@ant-design/icons";
import "antd/dist/reset.css";

import "../design/MainPageDesign.css";
import profileImage from "../assets/PFP.jpeg"; // Adjust the path as needed
import MatrixBackground from "./MatrixBackground";
import GeneralInfo from "./GeneralInfo";
import Skills from "./Skills";
import Services from "./Services";
import Projects from "./Projects";
import { Header } from "antd/es/layout/layout";

const { Content, Footer } = Layout;
const { Title, Text } = Typography;

// Contact information
const contactInfo = {
    email: "murad.aghamirzayevv@gmail.com",
    phone: "(+994) 55 354 53 13",
};

function UserOutlined() {
    return null;
}

function BookOutlined() {
    return null;
}

function SolutionOutlined() {
    return null;
}

const MainPage = () => {
    const [selectedKey, setSelectedKey] = useState("1");
    const [menuMode, setMenuMode] = useState(window.innerWidth <= 768 ? "inline" : "horizontal");

    // Function to update menu mode based on screen size
    const handleResize = () => {
        if (window.innerWidth <= 768) {
            setMenuMode("inline"); // mobile view
        } else {
            setMenuMode("horizontal"); // desktop view
        }
    };

    // Effect to listen for window resize events
    useEffect(() => {
        window.addEventListener("resize", handleResize);
        return () => {
            window.removeEventListener("resize", handleResize);
        };
    }, []);

    // Function to render the content based on the selected menu item
    const renderContent = () => {
        switch (selectedKey) {
            case "1":
                return <GeneralInfo />;  // General Info
            case "2":
                return <Projects />;  // Projects
            case "3":
                return <Skills />;  // Skills component
            case "4":
                return <Services />;  // Services component
            default:
                return <GeneralInfo />;
        }
    };

    return (
        <Layout>
            <MatrixBackground />
            <Content style={{ padding: "0 50px", marginTop: 64 }}>
                <div className="site-layout-content">
                    {/* Hero Section */}
                    <section className="hero" style={{ textAlign: "center", marginBottom: "50px" }}>
                        <Avatar size={120} src={profileImage} className="hero-avatar" style={{ borderColor: "#f0f0f0" }} />
                        <Title level={2} className="hero-title">Murad Aghamirzayev</Title>
                        <Text className="hero-subtitle">Full Stack Developer | BSCE at ADA University</Text>
                        <div style={{
                            marginTop: 16,
                            marginBottom: 5,
                            display: 'flex',
                            flexDirection: 'row',
                            justifyContent: 'center',
                            flexWrap: 'wrap'
                        }}>
                            <Button
                                type="primary"
                                icon={<GithubOutlined />}
                                href="https://github.com/muraddevs"
                                target="_blank"
                                style={{ marginRight: 10 }}
                            >
                                GitHub
                            </Button>
                            <Button
                                type="primary"
                                icon={<LinkedinOutlined />}
                                href="https://www.linkedin.com/in/murad-aghamirzayev-964494265/"
                                target="_blank"
                                style={{ marginRight: 10 }}
                            >
                                LinkedIn
                            </Button>
                            <Button
                                type="primary"
                                icon={<InstagramOutlined />}
                                href="https://www.instagram.com/agh_murad?igsh=MWhncDZvbWE5bWR5eQ=="
                                target="_blank"
                                style={{
                                    marginRight: 0,
                                    marginTop: window.innerWidth <= 768 ? 10 : 0
                                }} // Adjust margin based on screen size
                            >
                                Instagram
                            </Button>
                        </div>
                    </section>
                </div>

                {/* Menu for Navigation */}
                <Header
                    style={{ marginBottom: "50px", backgroundColor: "transparent", padding: 0, width: "100%", position: "relative",  }}>
                    <Menu
                        theme="light"
                        mode={menuMode}
                        defaultSelectedKeys={["1"]}
                        onClick={(e) => setSelectedKey(e.key)}
                        style={{ borderBottom: "none", display: "flex", justifyContent: "flex-start", width: "100%", padding: window.innerWidth <= 768 ? "0" : "0 24px", backgroundColor: "transparent", textAlign:"center" }} // Full width and no padding for mobile
                    >
                        <Menu.Item
                            key="1"
                            style={{ padding: window.innerWidth <= 768 ? "8px 12px" : "12px 24px", fontSize: window.innerWidth <= 768 ? 8 : 16, display: "flex", flex: "wrap" }} // Smaller padding for mobile
                        >
                            General Info
                        </Menu.Item>
                        <Menu.Item
                            key="2"
                            style={{ padding: window.innerWidth <= 768 ? "8px 12px" : "12px 24px", fontSize: window.innerWidth <= 768 ? 8 : 16 }} // Smaller padding for mobile
                        >
                            Projects
                        </Menu.Item>
                        <Menu.Item
                            key="3"
                            style={{ padding: window.innerWidth <= 768 ? "8px 12px" : "12px 24px", fontSize: window.innerWidth <= 768 ? 8 : 16}} // Smaller padding for mobile
                        >
                            Skills
                        </Menu.Item>
                        <Menu.Item
                            key="4"
                            style={{ padding: window.innerWidth <= 768 ? "8px 12px" : "12px 24px", fontSize: window.innerWidth <= 768 ? 8 : 16 }} // Smaller padding for mobile
                        >
                            Services
                        </Menu.Item>
                    </Menu>
                </Header>


                {/* Render the selected content */}
                {renderContent()}



            </Content>

            <Footer style={{ textAlign: "center", background: "#ffffff", borderColor: "#ffffff", marginTop: 0 }}>
                {/* Contact Info */}
                <Card
                    hoverable={false}
                    className="unhoverable-card"
                    style={{ marginTop: 40, textAlign: "left" }}
                >
                    <div style={{ marginBottom: 5 }}>
                        <Text style={{ fontSize: "30px", fontWeight: "bold", marginBottom: 10, textAlign: "left" }}>
                            Contact me:
                        </Text>
                    </div>
                    <br />
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        marginBottom: 8
                    }}>
                        <MailOutlined style={{ fontSize: "20px" }} />
                        <Text style={{
                            fontSize: window.innerWidth <= 768 ? 12 : 16,
                            fontWeight: "lighter",
                            marginLeft: 10
                        }}>{contactInfo.email}</Text>
                    </div>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        marginBottom: 8
                    }}>
                        <PhoneOutlined style={{ fontSize: "20px", marginRight: 5 }} />/
                        <WhatsAppOutlined style={{ fontSize: "20px", marginLeft: 5 }} />
                        <Text
                            style={{
                                fontSize: window.innerWidth <= 768 ? 12 : 16,
                                fontWeight: "lighter",
                                marginLeft: 10,
                            }}>{contactInfo.phone}</Text>
                    </div>
                </Card>
                <div style={{fontSize: window.innerWidth <= 768 ? 8 : 12, marginTop: 10}}>
                Murad Aghamirzayev 2024 | Created with Ant Design using React
                </div>
            </Footer>
        </Layout>
    );
};

export default MainPage;
